}

.timeline {
  overflow-x: hidden;
  overflow-y: hidden;
	background: linear-gradient(132deg, #FC415A, #591BC5, #212335);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
          animation: Gradient 15s ease infinite;
  position: relative;
  width: 100%;
  min-height: 100vh;
  /* max-width: 1140px; */
  margin: 0 auto;
  /* padding: 15px 0; */
  padding-top: 70px;
}

/* .stickysubform{
  position: sticky;

} */

.onform{
  border-radius: 50%;
  /* font-weight: 700; */
  background: #ffffff;
  color: #491e9a;
  border: 2px solid #f1f1f1;
  font-weight: 700;
  font-size: xx-large;
    width: 60px;
    aspect-ratio: 1/1;
}
.closeform{
  border-radius: 5px;
  background: #ffffff;
  color: #491e9a;
  border: 2px solid #f1f1f1;
  font-weight: 700;
}
.onform:hover{
  background-color: #F6D155;
  border: #F6D155;
  color: #491e9a;
}

.closeform:hover{
  border: 2px solid #6f20b6;

  background-color:#6f20b6;
  color: #ffffff;
}

.cube {
	position: absolute;
	top: 80vh;
	left: 45vw;
	width: 10px;
	height: 10px;
	border: solid 1px #D7D4E4;
	-webkit-transform-origin: top left;
	        transform-origin: top left;
	-webkit-transform: scale(0) rotate(0deg) translate(-50%, -50%);
	        transform: scale(0) rotate(0deg) translate(-50%, -50%);
	-webkit-animation: cube 12s ease-in forwards infinite;
	        animation: cube 12s ease-in forwards infinite;
  }
  .cube:nth-child(2n) {
	border-color: #FFF ;
  }
  .cube:nth-child(2) {
	-webkit-animation-delay: 2s;
	        animation-delay: 2s;
	left: 25vw;
	top: 40vh;
  }
  .cube:nth-child(3) {
	-webkit-animation-delay: 4s;
	        animation-delay: 4s;
	left: 75vw;
	top: 50vh;
  }
  .cube:nth-child(4) {
	-webkit-animation-delay: 6s;
	        animation-delay: 6s;
	left: 90vw;
	top: 10vh;
  }
  .cube:nth-child(5) {
	-webkit-animation-delay: 8s;
	        animation-delay: 8s;
	left: 10vw;
	top: 85vh;
  }
  .cube:nth-child(6) {
	-webkit-animation-delay: 10s;
	        animation-delay: 10s;
	left: 50vw;
	top: 10vh;
  }

@-webkit-keyframes Gradient {
	0% {
	  background-position: 0% 50%;
	}
	50% {
	  background-position: 100% 50%;
	}
	100% {
	  background-position: 0% 50%;
	}
  }

@keyframes Gradient {
	0% {
	  background-position: 0% 50%;
	}
	50% {
	  background-position: 100% 50%;
	}
	100% {
	  background-position: 0% 50%;
	}
  }

  @-webkit-keyframes cube {
	from {
	  -webkit-transform: scale(0) rotate(0deg) translate(-50%, -50%);
	          transform: scale(0) rotate(0deg) translate(-50%, -50%);
	  opacity: 1;
	}
	to {
	  -webkit-transform: scale(20) rotate(960deg) translate(-50%, -50%);
	          transform: scale(20) rotate(960deg) translate(-50%, -50%);
	  opacity: 0;
	}
  }

  @keyframes cube {
	from {
	  -webkit-transform: scale(0) rotate(0deg) translate(-50%, -50%);
	          transform: scale(0) rotate(0deg) translate(-50%, -50%);
	  opacity: 1;
	}
	to {
	  -webkit-transform: scale(20) rotate(960deg) translate(-50%, -50%);
	          transform: scale(20) rotate(960deg) translate(-50%, -50%);
	  opacity: 0;
	}
  }

.timeline::after {
  content: '';
  position: absolute;
  width: 2px;
  background: #006E51;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -1px;
}

.modules {
  padding: 15px 30px;
  position: relative;
  /* background: inherit; */
  width: 50%;
}

.modules.left {
  left: 0;
  padding-left: 100px;
}

.modules.right {
  left: 50%;
  padding-right: 100px;
}

.modules::after {
  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  top: calc(50% - 8px);
  right: -8px;
  background: #ffffff;
  border: 2px solid #006E51;
  border-radius: 16px;
  z-index: 1;
}

.modules.right::after {
  left: -8px;
}

.modules::before {
  content: '';
  position: absolute;
  width: 50px;
  height: 2px;
  top: calc(50% - 1px);
  right: 8px;
  background: #006E51;
  z-index: 1;
}

.modules.right::before {
  left: 8px;
}

.modules .date {
  position: absolute;
  display: inline-block;
  top: calc(50% - 8px);
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  color: #006E51;
  text-transform: uppercase;
  letter-spacing: 1px;
  z-index: 1;
}


.modules .content {
  padding: 30px 90px 30px 30px;
  background: #F6D155;
  position: relative;
  border-radius: 0 500px 500px 0;
}

.modules.right .content {
  padding: 30px 30px 30px 90px;
  border-radius: 500px 0 0 500px;
}
.modules .content h2 {
  margin: 0 0 10px 0;
  font-size: 18px;
  font-weight: normal;
  color: #006E51;
}
.modules .content p {
  margin: 0;
  font-size: 16px;
  line-height: 22px;
  color: #000000;
}

@media (max-width: 767.98px) {
  .timeline::after {
    left: 90px;
  }

  .modules {
    width: 100%;
    padding-left: 120px;
    padding-right: 30px;
  }

  .modules.right {
    left: 0%;
  }

  .modules.left::after, 
  .modules.right::after {
    left: 82px;
  }

  .modules.left::before,
  .modules.right::before {
    left: 100px;
    border-color: transparent #006E51 transparent transparent;
  }

  .modules.left .date,
  .modules.right .date {
    right: auto;
    left: 15px;
  }
  .modules.left .content,
  .modules.right .content {
    padding: 30px 30px 30px 90px;
    border-radius: 500px 0 0 500px;
  }
}
